<template>
  <!--  -->
  <div class="dayVisit">
    <!--  -->
    <van-row class="sub-header">
      <!--  -->
      <van-col class="text-left" span="18">&nbsp;</van-col>
      <!--  -->
      <van-col class="text-right" span="6">
        <span class="btn-right" @click="handleRightBtn">{{ baseInfo.rightBtn }}</span>
      </van-col>
    </van-row>
    <!--  -->
    <title-info :title="title" :remark="remark"></title-info>
    <!--  -->
    <el-table
    :data="tableData"
    :height="height"
    :empty-text="PROMPT.EMPTY_TEXT"
    header-cell-class-name="table-header"
    cell-class-name="table-cell"
    style="width: 100%"
    stripe>
      <el-table-column
      prop="album_title"
      label="1"
      min-width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="author"
      label="2"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="title"
      label="3"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="rank_change"
      label="4"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { NON_DYNAMIC_HEIGHT, PROMPT } from '@/utils'
import TitleInfo from '@/components/BaseTitleInfo'
import { testData } from '@/api'

export default {
  name: 'DayVisit',
  components: { TitleInfo },
  data () {
    return {
      PROMPT, // 
      baseInfo: { // 
        rightBtn: '1_' // 
      },
      title: '', // 
      remark: ['()', ''], // 
      tableData: [] // 
    }
  },
  computed: {
    height () { // (18 - )
      return NON_DYNAMIC_HEIGHT - 18 * this.remark.length
    },
    freshTime () {
      return new Date().toLocaleString()
    }
  },
  beforeMount () {
    this.init()
  },
  methods: {
    init () {
      this.remark[1] = ' ' + this.freshTime // 
      testData().then(response => {
        this.title = response.data.result[0].name
        this.remark[0] = response.data.result[0].color
        this.tableData = response.data.result[0].content
      })
    },
    handleRightBtn () { // 
      alert('sss')
    }
  }
}
</script>



